import Vue from "vue";
import App from "./manager.vue";
import router from "./router";
import store from "./store";
import {
  Loading,
  Table,
  TableColumn,
  Pagination,
  Form,
  FormItem,
  Input,
  Select,
  Option,
  Button
} from "element-ui";
import fetch from "../../untils/axios";
Vue.prototype.$http = fetch;
Vue.use(Loading);
Vue.use(Table);
Vue.use(TableColumn);
Vue.use(Pagination);
Vue.use(Form);
Vue.use(FormItem);
Vue.use(Input);
Vue.use(Select);
Vue.use(Option);
Vue.use(Button);
Vue.config.productionTip = false;
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
