import Vue from "vue";
import Router from "vue-router";
import PublicFunds from "./views/publicReserveFunds.vue";
import DrivingLice from "./views/drivingLicense.vue";
import DriversLice from "./views/driversLicense.vue";
import SocicalSecur from "./views/socialSecurity.vue";

Vue.use(Router);

export default new Router({
  // mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/pubfunds",
      name: "pubfunds",
      component: PublicFunds
    },
    {
      path: "/drivlicense",
      name: "drivlicense",
      component: DrivingLice
    },
    {
      path: "/driverslicense",
      name: "driverslicense",
      component: DriversLice
    },
    {
      path: "/socical",
      name: "socical",
      component: SocicalSecur
    }
  ]
});
